@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
  min-width: 320px;
  margin: 0; }

button {
  display: block;
  background-color: #0AD9C6;
  border: 0; }

.body-lock {
  overflow-y: scroll;
  position: fixed; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.header__bg {
  width: 100%;
  height: auto; }

.header__adv {
  position: absolute;
  width: 350px;
  top: 51.4%;
  left: 50%;
  transform: translate(-126%);
  color: #ffffff; }
  @media (max-width: 1189px) {
    .header__adv {
      top: 36.4%; } }
  @media (max-width: 1023px) {
    .header__adv {
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: auto;
      transform: translate(0); } }

@media (max-width: 1023px) {
  .header__logo {
    margin: 25px 0 0 21px; } }

@media (max-width: 767px) {
  .header__logo {
    margin: 25px 0 0 18px; } }

.header__title {
  font-size: 4.6rem;
  font-weight: 600;
  line-height: 1.4;
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 24px 0 24px 0; }
  @media (max-width: 1023px) {
    .header__title {
      width: 404px;
      margin: 82px auto 20px auto;
      font-size: 3rem;
      line-height: 1.4; } }
  @media (max-width: 767px) {
    .header__title {
      text-align: center;
      width: 288px;
      margin: 68px auto 20px auto; } }

.header__content {
  font-size: 2rem;
  font-weight: 300;
  line-height: 1.6;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .header__content {
      width: 400px;
      margin: auto;
      font-size: 1.8rem;
      line-height: 1.8;
      text-align: center; } }
  @media (max-width: 767px) {
    .header__content {
      width: 288px; } }

.nav {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  width: 100%;
  padding: 0 30px; }
  @media (max-width: 767px) {
    .nav {
      background-color: #ffffff; } }

.nav__list {
  display: flex;
  width: 873px;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  list-style: none;
  padding: 0 23px 11px 17px;
  margin: 26px auto 0 auto;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2); }
  @media (max-width: 1023px) {
    .nav__list {
      /*		width: 320px;
		margin: 57px auto 50px auto;
		padding: 0 16px;
		flex-direction: column;
		outline: 1px dashed tomato;*/
      position: absolute;
      top: 0;
      right: 0;
      display: block;
      width: 320px;
      margin: 0;
      padding: 57px 16px 50px 16px;
      background-color: #ffffff; } }

.nav__link {
  display: block;
  position: relative;
  text-decoration: none;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7);
  word-break: break-word; }
  @media (max-width: 1023px) {
    .nav__link {
      margin-top: 60px;
      font-size: 2rem;
      line-height: 1.4;
      letter-spacing: 0.02em;
      color: #48494D;
      padding-right: 30px; }
      .nav__link::after {
        content: '';
        position: absolute;
        top: 6px;
        right: 10px;
        width: 10px;
        height: 10px;
        border-right: 2px solid rgba(44, 46, 63, 0.5);
        border-bottom: 2px solid rgba(44, 46, 63, 0.5);
        transform: rotate(-45deg); } }

@media (max-width: 767px) {
  .nav__link--margin {
    margin-top: 63px; } }

.nav__toggle {
  top: 0;
  right: 0;
  position: relative;
  display: none;
  width: 50px;
  height: 50px; }

.nav__burger {
  position: absolute;
  top: 50%;
  right: 50%;
  margin-right: -11px;
  width: 22px;
  height: 1px;
  background-color: #000000;
  transition: 0.3s; }
  .nav__burger::before, .nav__burger::after {
    content: "";
    position: absolute;
    top: -7px;
    right: 0;
    width: 22px;
    height: 100%;
    background-color: inherit;
    transition: 0.3s; }
  .nav__burger::after {
    top: 7px; }

@media (max-width: 1024px) {
  .js-enable .nav__toggle {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5; }
  .js-enable .nav__list {
    display: none; } }

.nav--menu-active .nav__toggle {
  background-color: transparent; }

.nav--menu-active .nav__burger {
  width: 0;
  background-color: rgba(20, 21, 23, 0.2); }

.nav--menu-active .nav__burger::before {
  transform: translateY(7px) rotate(-45deg); }

.nav--menu-active .nav__burger::after {
  transform: translateY(-7px) rotate(45deg); }

.nav--menu-active .nav__list {
  display: block; }

.form {
  position: absolute;
  top: 49.9%;
  right: 50%;
  transform: translate(134%);
  width: 328px; }
  @media (max-width: 1189px) {
    .form {
      top: 34.9%; } }
  @media (max-width: 1023px) {
    .form {
      display: block;
      position: static;
      transform: translate(0);
      margin: auto; } }

.form__title {
  color: #ffffff;
  font-size: 1.7rem;
  font-weight: 600;
  letter-spacing: -0.025em;
  text-align: center;
  margin-bottom: 22px; }

.form input,
.form button[type="submit"] {
  border: none;
  width: 100%;
  border-radius: 4px; }

.form input {
  height: 48px;
  margin-bottom: 20px;
  padding: 0 30px; }
  .form input::placeholder {
    font-size: 1.5rem;
    color: rgba(28, 28, 28, 0.5);
    letter-spacing: 0.03em; }

.form button[type="submit"] {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.4;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  letter-spacing: -0.02em;
  height: 51px; }
